.App {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 650px;
    height: 400px;
    padding-left: 10px;
    margin: 50px auto;
    background-color: #FE0065;
    border-radius: 10px;
    box-shadow: 0 14px 45px rgba(0, 0, 0, 0.22), 0 18px 45px rgba(0, 0, 0, 0.22);
}
.title{
    text-align:center;
    margin-top: 100px;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    color: rgb(255, 196, 0);
    font-size: 50px;
    font-weight:bold;
    -webkit-text-stroke:2.5px rgb(40, 116, 202);;
}